footer {
    width: 100%;
    height: 166px;
    margin-top: 100px;
    background-color: #F7F7FB;
}

.footContainer {
    width: 70%;
    height: 100%;
    margin: auto;
    display: flex;
    gap: 56px;
    position: relative;
    padding-bottom: 64px;
    padding-top: 32px;
}

/* footLogo ===== */
.footLogo {
    width: 200px;
    height: 70px;
    opacity: .3;
}

.footLogo > a {
    display: block;
    width: 100%;
    height: 100%;
    text-indent: -9999em;
    background: url(../../../public/images/icon/logo.png) center/cover;
}

/* footTextContainer ===== */
.footTextContainer {
    display: flex;
    flex-direction: column;
    gap: 8px;
    width: 500px;
    height: 70px;
    font-size: 1.3rem;
}

.teamEmail {
    display: flex;
    gap: 15px;
    width: 100%;
    height: 24px;
}

.teamEmail > li {
    width: 100%;
    height: 100%;
}

.teamEmail > li > a {
    display: flex;
}

.teamEmail > li > a > img {
    width: 24px;
    height: 24px;
}

.teamEmail > li > a > span {
    flex-grow: 1;
    display: flex;
    justify-content: center;
    align-items: center;
}

/* footInfo ===== */
.footInfo {
    width: 200px;
    height: 15px;
    position: absolute;
    right: 0;
    font-size: 1.3rem;
    display: flex;
    justify-content: space-evenly;
    align-items: center;
}

/* footer hover ===== */
.footContainer > .footTextContainer a:hover {
    opacity: .5;
    border: 1px solid rgba(0, 0, 0, .5);
}

.footContainer > .footInfo a:hover {
    opacity: .5;
    border-bottom: 1px solid rgba(0, 0, 0, .5);
}